<template>
    <section class="layout">
        <CatalogComponent
                v-bind:category-id="categoryId" v-bind:sub-category-id="subCategoryId"
                v-bind:search-request="searchRequest"
                v-bind:basket-data="basket" @buy="onBuy"
                ref="catalog"/>
    </section>
</template>

<script>
    import { mapGetters, mapActions, mapMutations } from 'vuex';
    import modules from "../store/all.modules.name"
    import actions from "../store/all.actions.type";
    import mutations from "../store/all.mutations.type";

    import CatalogComponent from "./components/CatalogComponent";

    export default {
        name: "Catalog",

        components: {CatalogComponent},

        data: () => {
            return {
                error: '',
            }
        },

        props: {
            isComponent: {
                type: Boolean,
                default: false,
            },

            componentData: {
                type: Object,
                default: null,
            },

            basketData: {
                type: Array,
                default: null,
            },
        },

        methods: {
            ...mapActions(modules.catalog, [
                actions.catalog.ADD_TO_BASKET,
            ]),

            ...mapMutations(modules.catalog, [
                mutations.catalog.SET_IS_BASKET_MINIMIZED,
            ]),

            onBuy(item) {
                this.setIsBasketMinimized(false);
                this.addToBasket(item)
            },
        },

        computed: {
            ...mapGetters(modules.catalog, [
                "basket",
            ]),

            categoryId: function () {
                if (this.$route && this.$route.params && (this.$route.name === "catalog" || this.$route.name === "catalogWithSub")) {
                    return this.$route.params.categoryId;
                } else {
                    return null;
                }
            },

            subCategoryId: function () {
                if (this.$route !== null && this.$route.params !== null && this.$route.name === "catalogWithSub") {
                    return this.$route.params.subCategoryId;
                } else {
                    return null;
                }
            },

            searchRequest: function () {
                if (this.$route !== null && this.$route.params !== null && this.$route.name === "search") {
                    return this.$route.params.request;
                } else {
                    return null
                }
            },
        },

        watch: {
            '$route': {
                handler: function() {
                    let update = () => {
                        if (this.$refs && this.$refs.catalog) {
                            this.$nextTick(() => {
                                this.$refs.catalog.init();
                            });

                        } else {
                            setTimeout(update, 100);
                        }
                    };

                    update();
                },
                deep: true,
            }
        },
    }
</script>

<style lang="sass" scoped>
    @import '../assets/basic'

    .layout
        width: 90%!important
        margin: auto
        max-width: 1200px!important
</style>