<template>
    <section class="layout">

        <div class="sub-container">
            <div class="sub" v-bind:style="{'top': y + 'px'}">
                <img src="/logos/Homwest.png"/>
                <img src="/logos/kedr_logo.png"/>
                <img src="/logos/Logo_SteelArt.png"/>
                <img src="/logos/logo.png"/>
                <img src="/logos/logo_bastion.png"/>
            </div>
        </div>

        <CategoryChooseComponent class="category-choose-component"
                v-bind:data="categories"
                @onCategoryChoose="onCategoryChoose" @onCategoryExpand="onCategoryExpand"/>

        <div class="sub-container">
            <div class="sub" v-bind:style="{'top': y + 'px'}">
                <img src="/logos/MoDoors.png"/>
                <img src="/logos/Punto.png"/>
                <img src="/logos/somafix.png"/>
                <img src="/logos/mv_doors.png"/>
            </div>
        </div>
    </section>
</template>

<script>
    import { mapGetters, mapActions} from 'vuex';
    import actions from "../store/all.actions.type";
    import mutations from "../store/all.mutations.type";
    import modules from "../store/all.modules.name"

    import CategoryChooseComponent from "./components/CategoryChooseComponent";

    export default {
        name: "CategoryChoose",

        components: {CategoryChooseComponent},

        data: () => {
            return {
                y: 0
            }
        },

        methods: {
            ...mapActions(modules.catalog, [
                actions.catalog.LOAD_CATEGORIES,
            ]),

            onCategoryChoose(val) {
                if (!this.isComponent) {

                    if (val.subId) {
                        this.$router.push({ path: `/catalog/${val.id}/${val.subId}` });
                    } else {
                        this.$router.push({ path: `/catalog/${val.id}` });
                    }

                } else {
                    this.$emit('choose', {category: val.id, subCategory: val.subId})
                }
            },

            onCategoryExpand(val) {
                if (!this.isComponent) {
                    this.$store.commit(modules.catalog + "/" + mutations.catalog.CHOOSE_CATEGORY, val);
                }
            },

            handleScroll() {
                this.y = this.currentY();
            },

            currentY() {
                return (window.pageYOffset || document.documentElement.scrollTop) - (document.documentElement.clientTop || 0);
            },
        },

        computed: {
            ...mapGetters(modules.catalog, [
                "categories",
            ]),

            rawCategories: function () {
                let res = JSON.parse(JSON.stringify(this.categories));
                res.forEach((item) => {
                    if (item.isExpanded) {
                        item.isExpanded = false;
                    }
                });
                return res;
            },
        },

        created() {
            if (this.categories.length === 0) {
                this.loadCategories();
            }

            window.addEventListener('scroll', this.handleScroll);
        },

        destroyed() {
            window.removeEventListener('scroll', this.handleScroll);
        },
    }
</script>

<style lang="sass" scoped>
    .layout
        grid-gap: 25px
        display: grid
        grid-template-columns: 0.25fr 1fr 0.25fr
        width: 90%
        margin: auto
        max-width: 700px
        font-family: Roboto, sans-serif

        .category-choose-component
            margin: auto
            max-width: 500px

        .sub-container
            position: relative

            .sub
                position: absolute

                img
                    object-fit: cover
                    width: 100%
                    margin-top: 25px
                    margin-bottom: 25px
</style>